/**
 * Helper class to count frames per second.
 */
export class FPSController {
   __init() {this.count = 0}

   __init2() {this.lastTime = performance.now()}
  constructor(
      notify,
      window = 10,
  ) {;this.notify = notify;this.window = window;FPSController.prototype.__init.call(this);FPSController.prototype.__init2.call(this);}

  tick(time = performance.now()) {
    const window = this.window
    this.count = (this.count + 1) % window
    if (this.count === 0) {
      const fps = Math.floor((window * 1000) / (time - this.lastTime))
      this.notify(fps)
      this.lastTime = time
    }
  }
}
