import Grid from "@mui/material/Grid"
import React, { useCallback } from "react"
import {
  GenericDemo,


  SimpleCanvasRenderer,
} from "../sdfs/SimpleCanvasRenderer"

function Demo3D({
  frag,
  sx,
  animate,
  state,
  replacer,
}





) {
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const Renderer = useCallback(
    class Renderer2 extends SimpleCanvasRenderer  {
      constructor(canvas, onFps) {
        // eslint-disable-next-line @typescript-eslint/no-var-requires
        super(() => require("./" + frag + ".frag"), canvas, onFps)
      }

      updateShader() {
        let fs = this.fragShader().default
        if (replacer) {
          fs = fs.replace(
            "#define SDF(r, p) demoIcosahedron(r, p)",
            `#define SDF(r, p) ${replacer}(r, p)`,
          )
        }
        this.buildShader(
          // eslint-disable-next-line @typescript-eslint/no-var-requires
          require("../common/raymarch.vert").default,
          fs,
        )
      }
    },
    [frag, replacer],
  )
  return (
    React.createElement(GenericDemo, { sx: sx, Renderer: Renderer, animate: animate, state: state,} )
  )
}

export default () => {
  return (
    React.createElement(Grid, { container: true, style: { height: "99%" }, spacing: 2, padding: 2,}
      , [
        "demoTetrahedron",
        "demoCube",
        "demoOctahedron",
        "demoDodecahedron",
        "demoIcosahedron",
        "demoVector",
        "demoPlatonic",
        "demoLego",
      ].map((frag) => (
        React.createElement(Grid, { key: frag, item: true, xs: 12, md: 6, lg: 4,}
          , React.createElement(Demo3D, {
            frag: "demo3d",
            replacer: frag,
            sx: { height: 300 },
            animate: true,
            state: {},}
          )
        )
      ))
    )
  )
}
