/* eslint-disable @typescript-eslint/no-explicit-any */
export function memoizeLast(f) {
  let lastArgs = []
  let lastResult
  return function (...args) {
    if (
      lastArgs.length !== args.length ||
      lastArgs.some((v, i) => v !== args[i])
    ) {
      lastArgs = args
      lastResult = f(...args)
    }
    return lastResult
  } 
}
